

#tui-pagination-container .tui-page-btn {
    width: 40px;
    height: 40px;
    // border: 1px solid var(--primary-color-white);
    background: var(--primary-color-white);
    color: var(--text-color-black);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    font-weight: 500;
    border: none;
    font-size: 12px;
    line-height: 1.33px;
}

#tui-pagination-container .tui-page-btn.tui-is-selected {
    background-color: var(--accent-color);
    color: var(--primary-color-white);
}

#tui-pagination-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

#tui-pagination-container.is-hidden {
    display: none;
}