.preloader {
  width: 100%;
  height: 100%;
  z-index: 33;
  background: var(--primary-color-white);
  opacity: .9;
  visibility: visible;
  justify-content: center;
  align-items: center;
  transition: all 1s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader.done {
  opacity: 0;
  visibility: hidden;
}

.loader {
  z-index: 222;
  width: 50px;
  height: 50px;
  background: linear-gradient(45deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 40%, #e5f403);
  border-radius: 50%;
  animation: animate 1s linear infinite;
  position: absolute;
}

@media (min-width: 768px) {
  .loader {
    width: 150px;
    height: 150px;
  }
}

.loader:before {
  content: "";
  background: var(--primary-color-white);
  z-index: 1000;
  border-radius: 50%;
  position: absolute;
  top: 36px;
  bottom: 36px;
  left: 36px;
  right: 36px;
}

.loader:after {
  content: "";
  filter: blur(60px);
  z-index: 1;
  background: linear-gradient(45deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 40%, #e5f403);
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes animate {
  0% {
    filter: hue-rotate();
    transform: rotate(0deg);
  }

  100% {
    filter: hue-rotate(360deg);
    transform: rotate(360deg);
  }
}

.loader-hide {
  opacity: 0;
}

